"use client";
import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

const cards = [
  {
    title: "WHO ARE WE?",
    text: "Braselton Tech is a private-public initiative in partnership with the Advanced Technology Development Center (ATDC) headquartered at Georgia Tech."
  },
  {
    title: "OUR MISSION",
    text: "To develop a technology ecosystem that attracts technology companies, investors, and the talent required to scale."
  },
  {
    title: "WHAT IS ATDC?",
    text: "ATDC is a Georgia economic development organization funded by the state legislature and managed under the Georgia Tech Enterprise Organization Institute. It is the oldest technology business accelerator in the U.S. and its graduating companies have raised over $3 billion in venture capital."
  }
];

const images = [
  "https://xh0avnqjifo.makeswift.site/_next/image?url=https%3A%2F%2Fstorage.googleapis.com%2Fs.mkswft.com%2FRmlsZTo3MjJiZDBlZS1lZDI1LTRmMGYtOWEwMS0wZjU0M2NmZDI1YjE%3D%2FTeam%2520photo%2520cropped.jpg&w=3840&q=75",
  "https://xh0avnqjifo.makeswift.site/_next/image?url=https%3A%2F%2Fstorage.googleapis.com%2Fs.mkswft.com%2FRmlsZTpmZTA3NGMxYS0xZTY0LTQ3MWMtOGI1Zi1lOTZhYzJmMTc2NWM%3D%2Ftable%25202.jpeg&w=3840&q=75",
  "https://xh0avnqjifo.makeswift.site/_next/image?url=https%3A%2F%2Fstorage.googleapis.com%2Fs.mkswft.com%2FRmlsZTo1ZjYxYTgwMS05NzJjLTRkNjYtODU1Yy02NzgxYmEzODczNWM%3D%2FTable.jpeg&w=3840&q=75"
];

export default function FeaturesSection() {
  const [index, setIndex] = useState(0);

  function prev() {
    setIndex(i => (i === 0 ? images.length - 1 : i - 1));
  }
  function next() {
    setIndex(i => (i === images.length - 1 ? 0 : i + 1));
  }

  return (
    <section className="w-full py-20 px-4 md:px-0 max-w-7xl mx-auto">
      <div className="flex flex-col md:flex-row gap-14 items-stretch">
        {/* Info cards on the left */}
        <div className="flex-1 flex flex-col gap-10 justify-center">
          {cards.map((card, i) => (
            <motion.div
              key={card.title}
              initial={{ opacity: 0, y: 40 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.7, delay: i * 0.1 }}
              className="bg-white/90 rounded-3xl shadow-xl p-10 border border-blue-100"
            >
              <h2 className="text-2xl sm:text-3xl font-extrabold text-gray-800 mb-3 tracking-tight">{card.title}</h2>
              <p className="text-base sm:text-lg text-gray-700 font-sans leading-relaxed">{card.text}</p>
            </motion.div>
          ))}
        </div>
        {/* Image carousel on the right */}
        <motion.div
          className="flex-1 flex flex-col items-center justify-center"
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.7, delay: cards.length * 0.1 }}
        >
          <div className="w-full bg-white rounded-3xl shadow-2xl p-8 border border-blue-100 flex flex-col items-center">
            <div className="relative w-full max-w-lg aspect-[16/10] mx-auto overflow-hidden rounded-2xl">
              <AnimatePresence mode="wait">
                <motion.img
                  key={index}
                  src={images[index]}
                  alt={`Braselton Tech event photo ${index + 1}`}
                  className={`w-full h-full rounded-2xl object-cover${index === 0 ? ' object-top' : ''}`}
                  initial={{ opacity: 0, scale: 0.98 }}
                  animate={{ opacity: 1, scale: 1 }}
                  exit={{ opacity: 0, scale: 1.02 }}
                  transition={{ duration: 0.4 }}
                />
              </AnimatePresence>
              <button
                onClick={prev}
                className="absolute left-2 top-1/2 -translate-y-1/2 bg-white/80 hover:bg-blue-100 text-gray-800 rounded-full p-2 shadow transition"
                aria-label="Previous image"
              >
                &#8592;
              </button>
              <button
                onClick={next}
                className="absolute right-2 top-1/2 -translate-y-1/2 bg-white/80 hover:bg-blue-100 text-gray-800 rounded-full p-2 shadow transition"
                aria-label="Next image"
              >
                &#8594;
              </button>
            </div>
            <div className="flex gap-2 mt-4">
              {images.map((_, i) => (
                <button
                  key={i}
                  onClick={() => setIndex(i)}
                  className={`w-3 h-3 rounded-full ${i === index ? 'bg-blue-400' : 'bg-blue-100'} transition`}
                  aria-label={`Go to image ${i + 1}`}
                />
              ))}
            </div>
          </div>
        </motion.div>
      </div>
    </section>
  );
} 